.sidebar{
    display:flex;
}
.sidebarOne{
    background-color:rgb(251, 251, 255);
    height:120vh;
    width:250px;
}
.sidebarFeature{
    margin:40px;
}
.sidebarStories{
    margin:50px 0 0 1px;
}
.sidebarInformation img{
    width:50px;
    height:50px;
    border-radius:50%;
    border:1px solid rgb(180, 142, 92);
    object-fit:cover;
}
.sidebarTop{
    display:flex;
    flex-direction:column;
}
.sidebarInformation{
    display:flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top:40px;
    margin-bottom:50px;
}
.sidebarInformation h2{
    color: rgb(70, 70, 70);
    margin:6px 0 6px 0;
    font-size:22px;
}
.sidebarInformation span{
    color: rgb(145, 145, 145);
}
.sidebarFollowers{
    display: flex;
    align-items: center;
    margin: auto;
}
.sidebarFollower{
    display: flex;
    flex-direction:column;
    align-items: center;
    margin:14px;
}
.sidebarFollower h2{
    color: rgb(70, 70, 70);
    font-size:22px;
    margin-bottom:6px;
}
.sidebarFollower span{
    color: rgb(145, 145, 145);
}
.line{
    width:230px;
    margin: auto;
    margin-top:15px;
}
.sidebarCenter{
    display: flex;
    flex-direction: column;
    margin-top:15px;
}
.sidebarCenter ul{
    list-style:none;
}
.sidebarCenter ul li{
    padding:5px;
    cursor: pointer;
    display:flex;
    align-items:center;
    color: rgb(70, 70, 70);
    margin-top:18px;
}
.sidebarCenter .sideIcon{
    margin-right:14px;
}
.sidebarBottom{
    margin-top:60px;
    margin-left:40px;
    display:flex;
}
.sidebarSetting{
    margin-right:50px;
}
.sidebarSetting, .sidebarFriend{
    background-color:rgb(231, 231, 231);
    padding:16px;
    color: rgb(70, 70, 70);
    border-radius:10px;
    cursor: pointer;
}