.storySelect{
    display:flex;
    justify-content:space-between;
    align-items: center;
    margin-bottom:30px;
}
.storySelect select{
    width:150px;
    height:30px;
    border: none;
    border:1px solid rgb(233, 233, 233);
    background-color:rgb(247, 247, 247);
    color:rgb(90, 90, 90);
    border-radius:20px;
    outline:none;
}
.storySelect h2{
    margin-right:330px;
}
.storyimgs{
    display:flex;
}
.storyimg img{
    width:120px;
    height:160px;
    margin-right:11px;
    object-fit: cover;
}
.storyimg h3{
    font-size:14px;
    position: absolute;
    top:332px;
    color:#dcdcdc;
    margin-left: 10px;
}
.featFeed{
    margin-top:60px;
}
.featPhoto{
    display: flex;
    margin-top:18px;
}
.featPhoto img{
    width:210px;
    height:150px;
    margin-right:14px;
    object-fit: cover;
    border-radius:5px;
}