.feature{
    width:220px;
}
.topAvatar{
    width:100%;
    height:200px;
    object-fit: cover;
}
.featureCenter{
    margin-top:50px;
}
.featureCenter h2{
    font-size:24px;
    margin-bottom:8px;
    font-weight:600;
}
.featureCenter span{
    font-size:12px;
    font-weight:500;
    margin:8px;
}
.featureCenter p{
    color: rgb(145, 145, 145);
    margin-top:14px;
    font-weight:300;
}
.featureBottom{
    margin-top:20px;
}
.active, .location, .profiles{
    margin-bottom:10px;
}
.featureBottom h3{
    margin-bottom:6px;
    font-weight:500;
}
.featureBottom p{
    color: rgb(145, 145, 145);
    font-weight:300;
}

.featureBottom .imgs .bottomAvatar{
    width:40px;
    height:40px;
    margin-right:10px;
    object-fit: cover;
}