/* topbar */
.topbar{
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin:20px;
}
.topbarLogo{
    display:flex;
    align-items:center;
}
.topbarLogo .logo{
    margin-left:15px;
    color: rgb(180, 142, 92);
}
.topbarLogo h2{
    margin-left:15px;
    color: rgb(145, 145, 145);
}
.topbarLeft{
    display:flex;
    justify-content:space-between;
    align-items:center;
}
.topbarInput input{
    width:350px;
    height:50px;
    border-radius:40px;
    border:none;
    background-color: rgb(247, 247, 247);
    outline:none;
    font-size:20px;
    color: rgb(99, 99, 99);
}
.topbarInput input::placeholder{
    color: rgb(145, 145, 145);
}
.topbarIcons{
    margin-left:200px;
    color: rgb(145, 145, 145);
    cursor: pointer;
}
.topbarIcons .notification{
    font-size:33px;
    margin-left:20px;
}
.topbarIcons .menu{
    font-size:33px;
    margin-left:20px;
}